import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'followersCount' })
export class FollowersCountPipe implements PipeTransform {

    getNumber(value: number): string {
        if (value < 1000) return `${value}`;
        else if (value > 1000 && value < 1000000) return `${(value / 1000).toFixed(1)}k`.replace(',', '.');
        else return `${(value / 1000000).toFixed(2)}m`.replace(',', '.');
    }
    getClass(value: number): string {
        if (value < 10000) return 'gradient-red-numbers';
        else if (value > 10000 && value < 100000) return 'gradient-yellow-numbers';
        else return 'gradient-green-numbers';
    }
  transform(value: number): string {
    return `<span class='${this.getClass(value)}'>${this.getNumber(value)}</span>`    
  }
}
