import { Injectable } from '@angular/core';
import { ActivatedRoute, Data } from '@angular/router';
import { BehaviorSubject, takeUntil } from 'rxjs';
import { Watcher } from '../shared/classes/watcher';


@Injectable({
  providedIn: 'root'
})
export class UrlService extends Watcher {

  private pageTitleSubject: BehaviorSubject<string> = new BehaviorSubject<string>('')

  constructor(private activatedRoute: ActivatedRoute) {
    super()
  }

  setPageTitle(title: string) {
    this.pageTitleSubject.next(title);
  }

  getPageTitle(): string {
    return this.pageTitleSubject.value;
  }

  subscribeUrlCange() {
    this.activatedRoute.data
    .pipe(takeUntil(this.unsubscribe)).subscribe({
      next: (resolved: Data) => {
          // 
      }
    })
  }

  get titleSubj() {
    return this.pageTitleSubject;
  }

  unsubscribeObsv() {
    super.ngOnDestroy()
  }

}
