import { ChangeDetectionStrategy, ChangeDetectorRef, Component, HostListener, NgZone, OnInit } from '@angular/core';
import { ThemeService } from '../../services/theme.service';
import { UrlService } from '../../services/url.service';
import { SearchService } from '../../services/search.service';
import { Router, RoutesRecognized } from '@angular/router';
import { filter, pairwise, takeUntil } from 'rxjs';
import { Location } from '@angular/common'
import { Watcher } from '../../shared/classes/watcher';
import { PageTitle } from '../../core/types/page-title-options';
import { AuthService } from '../../services/auth.service';
import { DarkTheme, LightTheme } from '../../core/types/Themes';
import { TitleService } from '../../services/title.service';

@Component({
  selector: 'cake-app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.Default
})
export class HeaderComponent extends Watcher implements OnInit {
  isSearchOpened = false;

  pageTitle = '';

  redirectTo = '';

  isMobileHeaderVisible = false;

  isDesktopHeaderVisible = false;

  public get profileImg(): string {
    return this.authService.randomAvatar;
  }

  constructor(
    public themeService: ThemeService,
    private location: Location,
    private urlService: UrlService,
    private searchService: SearchService,
    private router: Router,
    private cdr: ChangeDetectorRef,
    public authService: AuthService,
    private zone: NgZone,
    public titleService: TitleService
  ) {
    super()
  }

  ngOnInit(): void {
    this.urlService.titleSubj.pipe(takeUntil(this.unsubscribe)).subscribe({
      next: (title: string) => {
        if (this.pageTitle !== title) {
          this.pageTitle = title

          this.cdr.detectChanges()
        }
      },
    });
    this.router.events.pipe(
      filter(event => event instanceof RoutesRecognized),
      pairwise(),
      takeUntil(this.unsubscribe)
    ).subscribe({
      next: events => {
        this.redirectTo = (events as RoutesRecognized[])[0].urlAfterRedirects
      }
    })
  }

  switchSearchState() {
    this.isSearchOpened = !this.isSearchOpened;
  }

  @HostListener('window:resize')
  onResize() {
    this.cdr.detectChanges();
  }

  get isMobileHeader() {
    return (this.pageTitle === PageTitle.grab && window.innerWidth <= 768) ||
      (this.pageTitle === PageTitle.rewards && window.innerWidth <= 768) ||
      (this.pageTitle === PageTitle.confectionary && window.innerWidth <= 768) ||
      (this.pageTitle === PageTitle.profile && window.innerWidth <= 768) ||
      (this.pageTitle === PageTitle.settings && window.innerWidth <= 768)
  }

  get isDesktopHeader() {
    return !this.pageTitle || (
      (this.pageTitle === PageTitle.grab && window.innerWidth > 768) ||
      (this.pageTitle === PageTitle.rewards && window.innerWidth > 768) ||
      (this.pageTitle === PageTitle.profile && window.innerWidth > 768) ||
      (this.pageTitle === PageTitle.settings && window.innerWidth > 768) ||
      (this.pageTitle === PageTitle.confectionary && window.innerWidth > 768)
    )
  }

  get isProfile() {
    return this.pageTitle === PageTitle.profile
  }

  get isSettings() {
    return this.pageTitle === PageTitle.settings
  }

  back() {
    if (this.redirectTo.includes('auth')) {
      this.zone.run(() => {
        this.router.navigateByUrl('/home')
      })
      return
    }
    this.location.back()
  }

  receiveFormControl() {
    return this.searchService.getFormControl();
  }

  //FOR MVP1
  changeTheme(boolean: boolean): void {
    this.themeService.changeActiveTheme(boolean ? DarkTheme : LightTheme)
  }
}
